<template>
  <div>
    <!-- Posts -->
    <v-row v-if="!$store.state.loading.blog.posts">
      <v-col v-for="(post, i) in getPosts" :key="i" :cols="getCols">
        <blog-post :post="post" />
      </v-col>
      <div v-if="postsLength < 1 && !$store.state.loading.blog.posts">
        <h2 class="display-1 text-center">Sorry no posts found</h2>
      </div>
    </v-row>
    <!-- Post Loader -->
    <v-row v-if="$store.state.loading.blog.posts">
      <v-col v-for="li in reading.postLimit" :key="li" :cols="getCols">
        <v-skeleton-loader
          :loading="$store.state.loading.blog.posts"
          type="card-avatar, article, actions"
          light
        >
        </v-skeleton-loader>
      </v-col>
    </v-row>
    <!-- Pagination -->
    <v-row>
      <v-col>
        <div v-if="postsLength > 0" class="text-center">
          <v-pagination
            v-model="$store.state.post.page"
            light
            :length="length"
            :total-visible="7"
            prev-icon="mdi-menu-left"
            next-icon="mdi-menu-right"
          ></v-pagination>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  components: {
    BlogPost: () => import('@/components/blog/posts/BlogPost')
  },
  data() {
    return {
      page: this.$route.query.page || 1
    }
  },
  computed: {
    ...mapState(['post']),
    ...mapState('settings', {
      reading: (state) => state.settings.blog.reading
    }),
    getCols() {
      const cols = this.$store.state.settings.settings.blog.layout.postCols
      if (cols === 4) {
        return 3
      } else if (cols === 3) {
        return 4
      } else if (cols === 2) {
        return 6
      } else {
        return 12
      }
    },
    getPosts() {
      // const posts = this.post.posts.slice(
      //   (this.page - 1) * this.limit,
      //   this.limit * this.page
      // )
      return this.post.posts
    },
    postsLength() {
      return this.post.posts.length
    },
    length() {
      return Math.ceil(this.post.count / this.reading.postLimit)
    }
  }
}
</script>

<style lang="scss" scoped></style>
